  font-size: 24px;
}

#mainText {
  color: white;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 18px;

}





button, .btn-fluid, .btn-secondary {
    margin: 10px;
    border: 2px solid teal;
    background-color: teal;
    color: white;
    padding: 5px 14px;
    font-size: 20px;
    cursor: pointer;
    font-family: 'Open Sans Condensed', sans-serif;
    box-shadow: 3px 3px 10px black;
    
}


.styled-marker {
  width: 30px;
  height: 30px;
  border-radius: 50% 50% 50% 0;
  background: #3d10e0;
  position: absolute;
  cursor: pointer;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  left: 50%;
  top: 50%;
  margin: -20px 0 0 -20px;
  -webkit-animation-name: bounce;
  animation-name: bounce;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
}
.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

